import { DatasourceOptions } from '../datasource.options';
import { QueryRunnerInterface } from './query-runner.interface';

/**
 * Driver organizes TypeORM communication with specific database management system.
 */
export interface Driver {
  /**
   * Connection options.
   */
  options: DatasourceOptions;

  /**
   * Database version/release. Often requires a SQL query to the DB, so it is not always set
   */
  version?: string;

  /**
   * Database name used to perform all write queries.
   *
   * todo: probably move into query runner.
   */
  database?: string;

  /**
   * Performs connection to the database.
   * Depend on driver type it may create a connection pool.
   */
  connect(): Promise<void>;

  /**
   * Makes any action after connection (e.g. create extensions in Postgres driver).
   */
  afterConnect(): Promise<void>;

  /**
   * Closes connection with database and releases all resources.
   */
  disconnect(): Promise<void>;

  /**
   * Creates a query runner used for common queries.
   */
  createQueryRunner(): QueryRunnerInterface;
}
